import axios from 'axios'
import { useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import fotoAlmacen from '../imagenes/almacenDefault.jpg'
import fotoObjeto from '../imagenes/objectDefault.jpg'
import fotoUsuario from '../imagenes/usuario.jpg'
import { Carousel } from 'bootstrap'


const URIuser = 'http://localhost:8000/usuarios/'

const CompRegistro = () =>{

    const [nombreUs, setNombreUs] = useState('')
    const [correo, setCorreo] = useState('')
    const [password, setPassword] = useState('')
    const [nombre, setNombre] = useState('')
    const [apellido, setApellido] = useState('')
    const [error, setError] = useState(null)

    const navigate = useNavigate()

    async function registro (e) {
        e.preventDefault()
        const res = await axios.get(URIuser)
        let users = res.data
        const existeNombre = users.find(usuario => nombreUs == usuario.nombreUsuario)
        const existeCorreo = users.find(usuario => correo == usuario.correo)
        if (existeNombre == undefined && existeCorreo == undefined) {
            await axios.post(URIuser, {
                nombreUsuario:nombreUs, 
                correo:correo, 
                contrasena:password,
                nombre:nombre,
                apellido:apellido
            })
            navigate(`/`)
        } else if (existeNombre == undefined){
            setError('Correo electrnico ya registrado')
        } else {
            setError('Nombre de usuario ya registrado')
        }   
        
    }

    const volverAtras = (e) => {
        e.preventDefault()
        navigate(-1)
    }

    return(
        <div className='d-flex align-items-center vh-100 fondoLogin'>
            <div className='container'>
                <div className='row'>
                    <div className='col-md-8'>
                        <div className='row'>
                            <div className='col'>
                                <div className='card' style={{padding: '20px', backgroundColor:'rgba(255, 255, 255, 0.7)'}}>
                                    <h1 style={{fontSize: '2.5rem', color: '#333'}}>Qu es VSR?</h1>
                                    <div className='d-flex text-start'>
                                        <p style={{fontSize: '1.25rem', color: '#555'}}>VSR es una aplicacin diseada para crear almacenes virtuales compartidos entre familiares, amigos y conocidos, permitindoles gestionar inventarios de distintos mbitos y sus respectivos objetos. Con VSR, los usuarios tienen la capacidad de cambiar objetos, editar su descripcin, condiciones de uso y ubicacin reciente, y tambin pueden reservar los objetos por un tiempo determinado. En resumen, VSR es una solucin de gestin de inventario en lnea altamente personalizable y colaborativa.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='row mt-4'>
                            <div className='col-md-4'>
                                <div className='card'>
                                    <img src={fotoAlmacen} style={{width: '100%', height:'100%', objectFit:'contain'}} className="card-img-top img-fluid"></img>
                                    <div className='card-title mt-2'>
                                        <h5>Almacenes</h5>
                                    </div>
                                </div>
                            </div>
                            <div className='col-md-4'>
                                <div className='card'>
                                    <img src={fotoObjeto} style={{width: '100%', height:'100%', objectFit:'contain'}} className="card-img-top img-fluid"></img>
                                    <div className='card-title mt-2'>
                                        <h5>Objetos</h5>
                                    </div>
                                </div>
                            </div>
                            <div className='col-md-4'>
                                <div className='card'>
                                    <img src={fotoUsuario} style={{width: '100%', height:'100%', objectFit:'contain'}} className="card-img-top img-fluid"></img>
                                    <div className='card-title mt-2'>
                                        <h5>Usuarios</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='col-md-4'>
                        <div className='card text-bg-light' style={{height:'100%'}}>
                            <div className='card-body'>
                                <div className='container'>
                                    <div className='row'>
                                        <div className='col'>
                                            <h3>Registro</h3>
                                        </div>
                                    </div>
                                    <div className='row'>
                                        <div className='col'>
                                            <form onSubmit={registro}>
                                                <div className='mb-3'>
                                                    <label className='form-label'>Nombre de usuario*</label>
                                                    <input
                                                        value={nombreUs}
                                                        onChange={ (e) => setNombreUs(e.target.value)}
                                                        type="text"
                                                        className='form-control'
                                                        style={{ width: 'auto', margin: '0 auto' }}
                                                        required='true'
                                                    />
                                                </div>
                                                <div className='mb-3'>
                                                    <label className='form-label'>Correo electrnico*</label>
                                                    <input
                                                        value={correo}
                                                        onChange={ (e) => setCorreo(e.target.value)}
                                                        type="text"
                                                        className='form-control'
                                                        style={{ width: 'auto', margin: '0 auto' }}
                                                        required='true'
                                                    />
                                                </div>
                                                <div className='mb-3'>
                                                    <label className='form-label'>Contrasea*</label>
                                                    <input
                                                        value={password}
                                                        onChange={ (e) => setPassword(e.target.value)}
                                                        type="password"
                                                        className='form-control'
                                                        style={{ width: 'auto', margin: '0 auto' }}
                                                        required='true'
                                                    />
                                                </div>
                                                <div className='mb-3'>
                                                    <label className='form-label'>Nombre*</label>
                                                    <input
                                                        value={nombre}
                                                        onChange={ (e) => setNombre(e.target.value)}
                                                        type="text"
                                                        className='form-control'
                                                        style={{ width: 'auto', margin: '0 auto' }}
                                                        required='true'
                                                    />
                                                </div>
                                                <div className='mb-5'>
                                                    <label className='form-label'>Apellido</label>
                                                    <input
                                                        value={apellido}
                                                        onChange={ (e) => setApellido(e.target.value)}
                                                        type="text"
                                                        className='form-control'
                                                        style={{ width: 'auto', margin: '0 auto' }}
                                                    />
                                                </div>
                                                {error && (
                                                <div className='row'>
                                                    <div className='col'>
                                                        <div className='alert alert-danger' role='alert'>
                                                            {error}
                                                        </div>
                                                    </div>
                                                </div>
                                                )}
                                                <button type='submit' className='btn primario'>Registrarse</button> <br/>
                                                <button onClick={volverAtras} className='btn btn-secondary mt-2'>Volver atrs</button> <br/>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CompRegistro